import { Button, Modal, Container, Row, Col, Form } from "react-bootstrap";
import { useState } from "react";
import api from "../../http/axios";

function CreateIncomeModal(props) {
  const [show, setShow] = useState(false);
  const [name, setName] = useState("");
  const [sum, setSum] = useState("");
  const [description, setDescription] = useState("");
  const [type, setType] = useState("one-time");

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function saveIncome() {
    const incomeData = JSON.stringify({
      name: name,
      sum: sum,
      description: description,
      type: type,
      createdBy: localStorage.getItem("userId"),
    });
    console.log(incomeData);
    api
      .post("/incomes", incomeData, {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + localStorage.getItem("jwt"),
        },
      })
      .then((res) => {
        handleClose();
        props.refreshIncomes();
      });
  }

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Create an Income
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Create an income</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Container>
            <Row>
              <Col sm={8} className="mt-1">
                <input
                  className="form-control"
                  placeholder="Name"
                  onChange={(e) => {
                    setName(e.target.value);
                  }}
                />
              </Col>
              <Col sm={4} className="mt-1">
                <input
                  className="form-control"
                  placeholder="Sum"
                  onChange={(e) => {
                    setSum(e.target.value);
                  }}
                />
              </Col>
              <Col sm={4}></Col>

              <Col sm={12} className="mt-3">
                <textarea
                  placeholder="Description"
                  rows={3}
                  className="form-control"
                  onChange={(e) => {
                    setDescription(e.target.value);
                  }}
                ></textarea>
              </Col>
              <Col sm={5} className="mt-2">
                <Form.Select
                  aria-label="Default select example"
                  onChange={(e) => {
                    setType(e.target.value);
                  }}
                >
                  <option value="one-time">One-time</option>
                  <option value="monthly">Monthly</option>
                </Form.Select>
              </Col>
            </Row>
          </Container>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={() => saveIncome()}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default CreateIncomeModal;
